<template>
  <v-card elevation="0">
    <v-form ref="form">
      <v-row class="px-3">
        <v-col v-if="renderComponent" class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              dense
              :rules="[$store.state.rules.required(title,'عنوان')]"
              color="txtf"
              label="عنوان"
              v-model="title"
          >

          </v-text-field>
        </v-col>
        <v-col v-if="renderComponent" class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              dense
              :rules="[$store.state.rules.required(desc,'توضیحات')]"
              color="txtf"
              label="توضیحات"
              v-model="desc"
          >

          </v-text-field>
        </v-col>
        <v-col v-if="renderComponent" class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              dense
              :rules="[$store.state.rules.required(plan_num,'شماره طرح')]"
              color="txtf"
              label="شماره طرح"
              v-model="plan_num"
          >

          </v-text-field>
        </v-col>
        <v-col v-if="renderComponent" class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              dense
              :rules="[$store.state.rules.required(floor_num,'شماره کف')]"
              color="txtf"
              label="شماره کف"
              v-model="floor_num"
          >

          </v-text-field>
        </v-col>
        <v-col v-if="renderComponent" class="py-0" cols="12" md="4">
          <v-text-field
              outlined
              dense
              :rules="[$store.state.rules.required(state_time,'مرتبه حالت')]"
              color="txtf"
              label="مرتبه حالت"
              v-model="state_time"
          >

          </v-text-field>
        </v-col>
        <v-col v-if="renderComponent" class="py-0" cols="12" md="4">
          <v-autocomplete
              outlined
              dense
              :items="state_types"
              item-text="name"
              item-value="id"
              item-color="info"
              return-object
              multiple
              :rules="[$store.state.rules.required(state_type,'عنوان')]"
              color="txtf"
              label="نوع حالت"
              v-model="state_type"
          >

          </v-autocomplete>
        </v-col>
        <v-col v-if="renderComponent" class="py-0" cols="12" md="4">
          <v-autocomplete
              outlined
              dense
              :items="requirements"
              item-value="id"
              item-text="title"
              item-color="info"
              multiple
              return-object
              :rules="[$store.state.rules.required(requirement,'نیازمندی')]"
              color="txtf"
              label="نیازمندی"
              v-model="requirement"
          >

          </v-autocomplete>
        </v-col>
        <v-col v-if="renderComponent" class="py-0" cols="12" md="4">
          <v-file-input
              outlined
              dense
              prepend-icon=""
              color="txtf"
              label="فایل"
              v-model="file"
          >

          </v-file-input>
        </v-col>
        <v-col class="py-0" cols="2" md="2">
          <v-btn  v-if="!id && ($store.state.permissions && $store.state.permissions['create-state'])" dark class="dash-btns dash-btns--blue" height="39" :disabled="loading" @click="addTo()">
            + افزودن
          </v-btn>
          <div class="d-flex" v-else>
            <v-btn text dark @click="editTo()" color="#17a2b8">
              <v-icon color="#17a2b8" :disabled="loading">mdi-check</v-icon>
              ویرایش
            </v-btn>
            <v-btn small height="36" text dark v-if="id" @click="cleanEdit()" color="red">
              <v-icon color="error" :disabled="loading">mdi-close</v-icon>
            </v-btn>
          </div>
        </v-col>
        <v-col class="py-0" cols="12">
          <v-data-table
              :headers="headers" :loading="loading" :items="data.data" item-key="id" disable-sort
          >

            <template v-slot:[`item.state_type`]="{item}">
              <div style="font-size: 12px" v-for="(stt,i) in item.state_type" :key="i+'statFor'">
                {{stt.name}} <span v-if="i!==(item.state_type.length-1)">, </span>
              </div>
            </template>

            <template v-slot:[`item.requirement`]="{item}">
              <div style="font-size: 12px" v-for="(req,i) in item.requirement" :key="i+'reqFor'">
                {{req.title}} <span v-if="i!==(item.requirement.length-1)">, </span>
              </div>
            </template>

            <template v-slot:[`item.id`]="{item}">
              <div class="d-flex justify-end">

                <a-sbtn v-if="$store.state.permissions && $store.state.permissions['change-state']" @clickBtn="toEdit(item)" color="#17a2b8"
                        icon="mdi-pencil" alt="ویرایش"/>
                <a-sbtn v-if="$store.state.permissions && $store.state.permissions['delete-state']" @clickBtn="toDelete(item.id,'/projects/api/v1/state/delete-state/')" color="#DC3545"
                        icon="mdi-delete" alt="حذف"/>

              </div>
            </template>

            <template v-slot:[`item.file`]="{item}">
              <a v-if="item.file" class="secondary--text" style="font-size: 12px" target="_blank" :href="item.file"> نمایش فایل </a>
              <span v-else >-</span>
            </template>

          </v-data-table>
        </v-col>
      </v-row>
    </v-form>
    <DeleteDialog @deleteFn="fetchDatas()" :ddata="deleteData"/>
  </v-card>
</template>

<script>
import ASbtn from "@/components/Dashboard/Misc/Components/a-sbtn";
import {forceRerender} from "@/components/Misc/forceRerender";
import axios from "axios";
import DeleteDialog from "@/components/Misc/DeleteDialog";

export default {
  name: "StatesApp",
  mixins: [forceRerender],
  components: {DeleteDialog, ASbtn},
  created() {
    this.fetchDatas();
    this.fetchReqs();
    this.fetchStateTypes();
  },
  data() {
    return {
      id: null,
      deleteData: {
        api: '',
        dialog: false,
        loading: false,
        idSelected: null,
      },
      title: null,
      plan_num: null,
      desc: null,
      floor_num: null,
      file: null,
      state_time: null,
      state_type: null,
      requirement: null,
      loading: false,
      data: {
        data: [],
      },
      state_types : null,
      requirements : null,
    }
  },
  methods: {
    fetchReqs(){
      axios.get(this.$store.state.host + '/projects/api/v1/requirement/requirements',{
        headers : {
          Authorization: `Bearer ${this.$store.state.userId}`,
        }
      }).then(response=>{
        this.requirements = response.data.data;
      }).catch(error =>{
        this.errorHandle(error.response);
      })
    },
    fetchStateTypes(){
      axios.get(this.$store.state.host + '/projects/api/v1/statetype/statetypes',{
        headers : {
          Authorization: `Bearer ${this.$store.state.userId}`,
        }
      }).then(response=>{
        this.state_types = response.data.data;
      }).catch(error =>{
        this.errorHandle(error.response);
      })
    },
    cleanEdit() {
      this.id = null;
      this.title = null;
      this.plan_num = null;
      this.desc = null;
      this.floor_num = null;
      this.file = null;
      this.state_time = null;
      this.state_type = null;
      this.requirement = null;
      this.forceRerender();
    },
    toDelete(id, api) {
      this.deleteData.idSelected = id;
      this.deleteData.api = api;
      this.deleteData.dialog = true;
    },

    toEdit(item) {
      this.title = item.title;
      this.plan_num = item.plan_num;
      this.desc = item.desc;
      this.floor_num = item.floor_num;
      this.file = null;
      this.state_time = item.state_time;
      this.state_type = item.state_type;
      this.requirement = item.requirement;
      this.id = item.id;
    },

    addTo() {
      this.$refs.form.validate();
      if (this.$refs.form.validate()) {
        if (this.file.size > 10485760) {
          this.notify('حجم فایل بیشتر از 10 م.گ', 'error')
          return
        }
        let formData = this.formData();
        this.loading = true;
        this.forceRerender();
        axios.post(this.$store.state.host + '/projects/api/v1/state/create-state', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
            Accept: "*/*",
            Authorization: `Bearer ${this.$store.state.userId}`,
          }
        }).then(() => {
          this.notify('حالت اضافه شد', 'info');
          this.fetchDatas();
        }).catch(error => {
          this.errorHandle(error.response);
        }).finally(() => {
          this.loading = false;
        })
      }
    },
    formData() {

      let data = {
        title: this.title,
        plan_num: this.plan_num,
        desc: this.desc,
        floor_num: this.floor_num,
        state_time: this.state_time,
      }

      let formData = new FormData();

      if (this.file && (typeof this.file === 'object')) {
        formData.append('file', this.file);
      }
      Object.keys(data).forEach(key => {
        formData.append(key, data[key]);
      })

      const state_type = this.state_type.map(item => item.id);

      Object.keys(state_type).forEach((key) => {
        formData.append('state_type', state_type[key]);
      })

      const requirement = this.requirement.map(item => item.id);

      Object.keys(requirement).forEach((key) => {
        formData.append('requirement', requirement[key]);
      })


      this.title = null;
      this.plan_num = null;
      this.desc = null;
      this.floor_num = null;
      this.file = null;
      this.state_time = null;
      this.state_type = null;
      this.requirement = null;

      return formData

    },
    editTo() {

      this.$refs.form.validate();
      if (this.$refs.form.validate()) {
        if (this.file.size > 10485760) {
          this.notify('حجم فایل بیشتر از 10 م.گ', 'error')
          return
        }
        let formData = this.formData();
        this.loading = true;
        const id = this.id;
        this.id = null;
        this.forceRerender();
        axios.patch(this.$store.state.host + '/projects/api/v1/state/change-state/' + id, formData, {

          headers: {
            'Content-Type': 'multipart/form-data',
            Accept: "*/*",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },

        }).then(() => {
          this.notify('ویرایش شد', 'warning');
          this.fetchDatas();
        }).catch(error => {
          this.errorHandle(error.response)
        })
      }
    },
    fetchDatas() {
      this.loading = true;
      axios.get(this.$store.state.host + '/projects/api/v1/state/states', {
        headers: {
          Authorization: `Bearer ${this.$store.state.userId}`,
        }
      }).then(response => {
        this.data = response.data;
        console.log(this.data.data)
      }).catch(error => {
        this.errorHandle(error.response);
      }).finally(() => {
        this.loading = false;
      })
    }
  },
  computed: {
    headers() {
      return [
        {
          text: 'نوع حالت',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'title',
        },
        {
          text: 'توضیحات',
          align: 'center',
          class: 'header-table-gray',
          sortable: false,
          value: 'desc',
        },{
          text: 'ش.طرح',
          align: 'center',
          class: 'header-table-gray',
          sortable: false,
          value: 'plan_num',
        },{
          text: 'ش.کف',
          align: 'center',
          class: 'header-table-gray',
          sortable: false,
          value: 'floor_num',
        },{
          text: 'مرتبه',
          align: 'center',
          class: 'header-table-gray',
          sortable: false,
          value: 'state_time',
        },{
          text: 'نوع',
          align: 'center',
          class: 'header-table-gray',
          sortable: false,
          value: 'state_type',
        },{
          text: 'نیازمندی',
          align: 'center',
          class: 'header-table-gray',
          sortable: false,
          value: 'requirement',
        },{
          text: 'فایل',
          align: 'center',
          class: 'header-table-gray',
          sortable: false,
          value: 'file',
        },
        {
          text: 'عملیات', class: 'header-table-gray',
          align: 'end',
          value: 'id',
        },


      ]
    },
  },
}
</script>

